<template>
  <!-- search部分 -->
  <div class="search">
    <input
      type="text"
      placeholder="请输入城市名字"
      spellcheck="false"
      v-model="cityName"
    />
    <button @click="getmsg"><img src="./images/search.png" alt="" /></button>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "searchComponents",
  data() {
    return {
      cityName: "",
    };
  },
  methods: {
    getmsg() {
      if (!this.cityName.trim()) {
        return;
      } else {
        const apiKeys = "cf0f522e60bb809b8ac46c459efa6f76";
        const getLatLot = `http://api.openweathermap.org/geo/1.0/direct?q=${this.cityName}&appid=${apiKeys}`;
        axios.get(getLatLot).then(
          (response) => {
            let lat = response.data[0].lat;
            let lon = response.data[0].lon;
            const getWeather = `http://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&appid=${apiKeys}&units=metric`;
            axios.get(getWeather).then(
              (response) => {
                const weatherObj = {
                  imgpath: `http://api.openweathermap.org/img/wn/${response.data.weather[0].icon}@2x.pn}`,
                  temp: response.data.main.temp + "℃",
                  humidity: response.data.main.humidity,
                  speed: response.data.wind.speed,
                  cityName: this.cityName,
                };
                this.$bus.$emit("sendweatherObj", weatherObj);
              },
              (error) => {
                console.log("错误信息", error.message);
              }
            );
          },
          (error) => {
            console.log("错误信息", error.message);
          }
        );
      }
    },
  },
};
</script>

<style>
/*搜索框样式*/
.search {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.search input {
  border: 0;
  outline: 0;
  background: #ebfffc;
  color: #555;
  padding: 10px 25px;
  height: 60px;
  border-radius: 30px;
  flex: 1;
  margin-right: 16px;
  font-size: 18px;
}

.search button {
  border: 0;
  outline: 0;
  background: #ebfffc;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  cursor: pointer;
}

.search button img {
  width: 16px;
}
</style>