<template>
  <!-- weather部分 -->
  <div class="weatherComponents" v-bind="weatherObj" v-if="weatherObj && weatherObj.temp && weatherObj.cityName">
    <img src="./images/Clear.png" class="weather-icon" />
    <h1 class="temp">{{ weatherObj.temp }}</h1>
    <h2 class="city">{{ weatherObj.cityName }}</h2>
    <div class="details">
      <div class="col">
        <img src="./images/humidity.png" alt="" />
        <div>
          <p class="humidity">{{ weatherObj.humidity }}</p>
          <p>湿度</p>
        </div>
      </div>
      <div class="col">
        <img src="./images/wind.png" alt="" />
        <div>
          <p class="humidity">{{ weatherObj.speed }}</p>
          <p>风力</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      weatherObj: {
        temp: "",
        cityName: "",
        humidity: "",
        speed: "",
      },
    };
  },
  mounted() {
    this.$bus.$on("sendweatherObj", (weatherObj) => {
      this.weatherObj = weatherObj;
    });
  },
  beforeDestroy() {
    this.$bus.$off("sendweatherObj");
  }
};
</script>

<style>
/*天气样式*/
.weather-icon {
  width: 170px;
  margin-top: 30px;
}

.weather h1 {
  font-size: 80px;
  font-weight: 500;
}

.weather h2 {
  font-size: 45px;
  font-weight: 400;
  margin-top: -10px;
}

.details {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  margin-top: 50px;
}

.col {
  display: flex;
  align-items: center;
  text-align: left;
}

.col img {
  width: 40px;
  margin-right: 10px;
}

.humidity,
.wind {
  font-size: 28px;
  margin-top: -6px;
}
</style>